.container-desktop{
    padding-top: 65px;
}
.rigth-menu{
    background-color: aqua;
}

.icon-box{
    display: flex;
    flex-direction: column;
  }
  
  .icon-box-col{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    padding-left: 2.5px;
  }
  .icon-box-col-title{
    display: flex;
    width: 100%;
    justify-content: center;
    color: antiquewhite;
    padding-left: 3.5px;
    
  }

  .container-w{
    position: absolute;
    top:80px;
    bottom: 0px;
    left:25px;
    display: flex;
    flex-direction: column;
    width: 40%;
    height: 59%;
    z-index: 10;
  }
  .container-w-col-1{

    min-height: 30px;
    height: 30px;
    background-color: rgb(51, 49, 49);
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    width: 100%;
    box-sizing: content-box;
    justify-content: flex-end;
    align-items: center;
    
  }
  .button{
    position: relative;
    right: 15px;
    border-radius: 50%;
    background-color: red;
    outline: none;
    border: none;
    width: 15px;
    height: 15px;
  }
  .button:hover{
    cursor: pointer;

  }
  .container-w-col-2{
    display: flex;
    box-sizing: content-box;
    width: 100%;
    min-height: 100%;
    height: 100%;
    background-color: rgba(46, 44, 44, 0.404);
    backdrop-filter: blur(5px);
  }