
.works-page-container-w{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    min-height: 100%;
    margin-top: 45px;
    z-index: 100;
  }
  .works-page-container-w-col-1{

    min-height: 30px;
    height: 30px;
    background-color: rgb(51, 49, 49);
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    width: 100%;
    box-sizing: content-box;
    justify-content: flex-end;
    align-items: center;
    
  }
  .works-page-button{
    position: relative;
    right: 15px;
    border-radius: 50%;
    background-color: red;
    outline: none;
    border: none;
    width: 15px;
    height: 15px;
  }
  .works-page-button:hover{
    cursor: pointer;

  }
  .works-page-container-w-col-2{
    display: flex;
    box-sizing: content-box;
    width: 100%;
    min-height: 100%;
    height: 100vh;
    background-color: rgba(46, 44, 44, 0.404);
    backdrop-filter: blur(5px);
  }

  .works-page-container-w-col-2-main{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-around;
    color: antiquewhite;
    padding: 0.5em;
    align-items: flex-start;
    align-content: flex-start;
  }

  .works-cards{
    width: 250px;
    height: 250px;
    margin: 0.5em;
    box-shadow: 5px 5px 10px black;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .works-cards-title{
    width: 100%;
    background-color: rgba(0, 0, 0, 0.863);
    box-shadow: 5px 5px 10px black;
    display: flex;
    justify-content: center;
    align-items: center;
  }