
.about-page-container-w{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    min-height: 100%;
    margin-top: 45px;
    z-index: 100;
  }
  .about-page-container-w-col-1{

    min-height: 30px;
    height: 30px;
    background-color: rgb(51, 49, 49);
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    width: 100%;
    box-sizing: content-box;
    justify-content: flex-end;
    align-items: center;
    
  }
  .about-page-button{
    position: relative;
    right: 15px;
    border-radius: 50%;
    background-color: red;
    outline: none;
    border: none;
    width: 15px;
    height: 15px;
  }
  .about-page-button:hover{
    cursor: pointer;

  }
  .about-page-container-w-col-2{
    display: flex;
    box-sizing: content-box;
    width: 100%;
    min-height: 100%;
    height: 100vh;
    background-color: rgba(46, 44, 44, 0.404);
    backdrop-filter: blur(5px);
  }