.firstmenu{
    position: absolute;
    top:45px;
    left:5px;
    display: flex;
	flex-direction: column;
    width: 200px;
    height: auto;
    background-image: linear-gradient(to top, #aeb1b3 0%, #e2ebf0 100%);
    z-index: 1000;
	box-shadow: 0px 0px 15px rgb(48, 47, 47);
	padding:10px;
	border-radius: 5px;

}
.firstmenu-submenu-on{
	position: absolute;
    top:215px;
    left:230px;
    display: flex;
	flex-direction: column;
    width: 200px;
    height: 100px;
    background-image: linear-gradient(to top, #aeb1b3 0%, #e2ebf0 100%);
    z-index: 1000;
	box-shadow: 0px 0px 15px rgb(48, 47, 47);
	padding:10px;
	border-radius: 5px;
}
.hr{
	color: black;
	background-color: black;
}
.firstmenu-col{
	width: 100%;
	height: 30px;
	min-height: 30px;
	box-sizing: content-box;
	display: flex;
	align-items: center;

	&:hover{
		background-color: #b3b3b48e;
		cursor: pointer;
		padding-left: 5px;
	}

	img{
		width: 20px;
	}

}
.firstmenu-col-about{
	border-bottom: 1px solid rgb(94, 92, 92);
}

.firstmenu-slideDown{
	animation-name: slideDown;
	-webkit-animation-name: slideDown;	

	animation-duration: 1s;	
	-webkit-animation-duration: 1s;

	animation-timing-function: ease;	
	-webkit-animation-timing-function: ease;	

	visibility: visible !important;						
}

@keyframes slideDown {
	0% {
		transform: translateY(-100%);
		opacity: 0;
	}
	50%{
		transform: translateY(8%);
	}
	65%{
		transform: translateY(-4%);
	}
	80%{
		transform: translateY(4%);
	}
	95%{
		transform: translateY(-2%);
	}			
	100% {
		transform: translateY(0%);
		opacity: 1;
		
	}		
}