.topnavbar{
    position: absolute;
    top:0;
    left: 0;
    display: flex;
    width: 100%;
    justify-content: space-around;
    min-height: 45px;
    max-height: 45px;
    // background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
    background-image: linear-gradient(to top,  #e2ebf000 0%, #cfd9df38 100%);
    // background-image: linear-gradient(to top, lightgrey 0%, lightgrey 1%, #e0e0e0 26%, #efefef 48%, #d9d9d9 75%, #bcbcbc 100%);
    box-shadow: 0px 5px 5px rgba(100, 100, 100, 0.171);
    backdrop-filter: blur(5px);
    z-index: 2;
}
.topnavbar-logo{
    width: 30px;
    height: 30px;
    margin: 0.2em;
}
.topnavbar-col-1{
    display: flex;
    width: 15%;
    justify-content: left;
    align-items: center;
    padding-left: 10px;
    cursor: pointer;
}
@import url('https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap');
.topnavbar-col-2{
    display: flex;
    width: 65%;
    justify-content: center;
    align-items: center;
    h4{
        font-family:  'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        color: rgb(53, 51, 51);
        margin: 0;
        padding: 0;
        font-size: 0.9rem;
        text-shadow: 0px 0px 5px gray;
    }
}

.topnavbar-col-3{
    display: flex;
    width: 20%;
    justify-content: flex-end;
    align-items: center;
    padding-right: 15px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif, cursive;
    cursor: pointer;
    color: antiquewhite;
}
.topnavbar-menu-icon{
    border-radius: 50%;
    padding: 0.1em;
    &:hover{
        background-color: rgba(130, 131, 133, 0.212);
    }
}
.topnavbar-menu-icon-active{
    border-radius: 50%;
    padding: 0.1em;
    background-color: rgba(130, 131, 133, 0.212);
}