.intro-main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    margin-top: auto;
    margin-bottom: auto;
  }
  .intro-profile-img-div{
    border-radius: 50%;
    border: 2px dotted rgb(80, 80, 80);
  }
  
  .intro-profile-img-div:hover{
    animation-name: pulse;
      -webkit-animation-name: pulse;	
    animation-delay: 0.2s;	
      animation-duration: 1.5s;	
      -webkit-animation-duration: 1.5s;
  
      animation-iteration-count: infinite;
      -webkit-animation-iteration-count: infinite;
    cursor: pointer;
  }
  
  .intro-profile-img-div:active{
      animation-name: pulse;
      -webkit-animation-name: pulse;	
  
      animation-duration: 1.5s;	
      -webkit-animation-duration: 1.5s;
  
      animation-iteration-count: infinite;
      -webkit-animation-iteration-count: infinite;
  }
  
  .intro-profile-img{
    width: 150px;
    border-radius: 50%;
    border: 15px double rgb(80, 80, 80);
    filter: contrast(120%);
    filter: blur(0.5px);
    box-shadow: 0px 0px 5px black;
  }